<template>
  <div class="read-more">
    <div style="text-align: center">
      <h1>Vue.js Read More</h1>
      
      <p>Read-More is a simple plugin that has been created with &#10084; by a bored developer.</p>
      <a class="github-button" href="https://github.com/orlyyani/read-more" data-size="large" data-show-count="true" aria-label="Star orlyyani/read-more on GitHub" style="margin-bottom: 20px;">Star</a>

      <div style="width: 50%; margin: 0 auto; margin-top: 20px;">
        <!-- <p v-readMore:25="msg"></p> -->
        <h4>With Read Less</h4>
        <read-more :text="msg" less-str="read less" :max-chars="200"></read-more>
        <h4>Without Read Less</h4>
        <read-more :text="msg" :max-chars="50"></read-more>
        <h4>Redirect</h4>
        <read-more :text="msg" :max-chars="150" :link="'https://github.com/orlyyani/read-more'"></read-more>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'read-more',
  data () {
    return {
      msg: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.'
    }
  }
}
</script>



