import Vue from 'vue'
import App from './App'
import router from './router'
import ReadMore from 'vue-read-more';
import PhotoGrid from 'vue-photo-grid';

Vue.config.productionTip = false

Vue.use(ReadMore);
Vue.use(PhotoGrid);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



